#roundButton {
  position: fixed;
  top: 0;
  right: 0;
  padding: 3px;
  background-color: #96d9ff;
  margin-top: 10px;
  margin-right: 20px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 10px solid #0065a6;
  outline: none;
  -webkit-transition: all 0.2s cubic-bezier(0, 1.26, 0.8, 1.28);
  -o-transition: all 0.2s cubic-bezier(0, 1.26, 0.8, 1.28);
  transition: all 0.2s cubic-bezier(0, 1.26, 0.8, 1.28);
  color: #2b3033;
}

#roundButton:hover {
  background-color: #96d9ff;
  cursor: pointer;
  border-color: #003557;
  -webkit-transform: scale(1.2, 1.2);
      -ms-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
}

#roundButton:active {
  border-color: #003557;
  background-color: #fff;
}



